<template>
  <div class="my">
    <my-page v-if="userToken" />

    <not-login :sendinfolist="infoList" v-else>
      <img src="../assets/my/cloud.png" slot="img1" />
      <img src="../assets/my/equipment.png" slot="img2" />
    </not-login>
  </div>
</template>

<script>
import NotLogin from "../components/NotLogin/NotLogin";
import MyPage from "../components/MyPage/MyPage";
import { request } from "../network/request";
export default {
  name: "My",
  components: {
    NotLogin,
    MyPage,
  },
  computed: {
    userToken() {
      return this.$store.state.userToken;
    },
    userPhone() {
      return this.$store.state.userPhone;
    },
    userPassword() {
      return this.$store.state.userPassword;
    },
  },
  created() {
    let timestamp = new Date().getTime();
    request({
      url: `/login/cellphone?phone=${this.userPhone}&password=${this.userPassword}&timestamp=${timestamp}`,
    }).then((response) => {
      this.$store.commit("changeValue", [
        {
          type: "userToken",
          value: response.token,
        },
        {
          type: "userId",
          value: response.account.id,
        },
        {
          type: "userAvatarUrl",
          value: response.profile.avatarUrl,
        },
      ]);
    });
  },
  data() {
    return {
      infoList: {
        d1: "登录网易云音乐",
        d2: "查看并管理你收藏的私房音乐，",
        d3: "方便地随时随地收听",
      },
    };
  },
};
</script>
