<template>
  <div class="nmain">
    <div
      v-for="(value, index) in navPlayList"
      :key="index"
      @click="jumpPage('myplaylist', value.id)"
    >
      <div class="nitem" v-if="!cancleIndex.some(ele => {return ele === index} )">
        <img :src="value.coverImgUrl" />
        <div class="navinfo">
          <span v-if="index === 0 && navReName">我喜欢的音乐</span>
          <span v-else>{{value.name}}</span>
          <span>
            {{value.trackCount}}首
            <span
              v-if="!navReName"
              class="nlnickname"
            >by {{value.creator.nickname}}</span>
          </span>
        </div>
        <div class="nlop" @click="cancleSub(value.id, index)">
          <img src="../../assets/my/clear_black.png" />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { request } from "../../network/request";
export default {
  name: "NavList",
  data() {
    return {
      plId: 0,
      currentIndex: null,
      cancleIndex: [],
    };
  },
  props: {
    navPlayList: {
      type: Array,
      default: () => {
        return [];
      },
    },
    navReName: {
      type: Boolean,
      default: false,
    },
  },
  computed: {
    operateFlag() {
      return this.$store.state.operateFlag;
    },
  },
  watch: {
    operateFlag(newVal) {
      if (newVal === true && this.plId) {
        request({
          url: `/playlist/delete?id=${this.plId}`,
        }).then((response) => {
          this.$store.commit("changeValue", {
            type: "operateFlag",
            value: false,
          });
          this.cancleIndex.push(this.currentIndex);
        });
      }
    },
  },
  methods: {
    jumpPage(path, id) {
      this.$store.commit("changeValue", {
        type: "userPageFlag",
        value: this.navReName,
      });
      this.$router.push({
        name: path,
        params: {
          id: id,
        },
      });
    },
    cancleSub(id, index) {
      this.plId = id;
      this.currentIndex = index;
      this.$store.commit("changeValue", [
        {
          type: "operateShow",
          value: true,
        },
        {
          type: "windowHead",
          value: "提示",
        },
      ]);
    },
  },
};
</script>

<style>
.nlnickname {
  display: inline-block;
  width: 90px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.navinfo > span:nth-of-type(2) {
  color: #999;
}
.navinfo > span:nth-of-type(1) {
  width: 130px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.navinfo {
  display: flex;
  font-size: 12px;
  flex-direction: column;
  justify-content: space-between;
  font-weight: initial;
  height: 40px;
  line-height: initial;
  margin-left: 10px;
}
.nlop {
  position: absolute;
  top: 12px;
  left: 210px;
  opacity: 0;
  cursor: pointer;
}
.nlop:hover {
  opacity: 1;
}
.nitem > img {
  width: 40px;
  height: 40px;
}
.nitem:hover > .nlop {
  opacity: 0.5;
}
.nitem:hover {
  background-color: #f4f2f2;
}
.nitem {
  display: flex;
  margin-top: 15px;
  cursor: pointer;
  width: 240px;
  margin-left: -40px;
  padding-left: 20px;
  position: relative;
}
</style>