<template>
  <div class="mynav">
    <div v-for="(value, index) in myList" :key="index">
      <div :class="currentIndex === index && index < 3?'mpactive mpitem':'mpitem'">
        <div>
          <div class="mpimg" v-if="index > 2">
            <img
              src="../../assets/arrow/myarrow.png"
              :style="openIndex[index-3]?'transform: rotateZ(90deg)': ''"
            />
          </div>
          <div @click="toggleMyPage(pathList[index], index)">
            {{value}}
            <span v-if="index > 2" class="mpreg">&reg;</span>
            <div v-if="index === 3" class="mpnew" @click="createPlayList">
              <span>+</span>
              <span>新建</span>
            </div>
            （{{subCount[countList[index]]}}）
          </div>
        </div>

        <div v-show="openIndex[index - 3]">
          <nav-list :navPlayList="createPLC" :navReName="true" v-if="index === 3" />
          <nav-list :navPlayList="subPLC" v-if="index === 4" />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { request } from "../../network/request";
import NavList from "./NavList";
export default {
  name: "MyPageNav",
  components: {
    NavList,
  },
  data() {
    return {
      subCount: {},
      currentIndex: 0,
      openIndex: [false.false],
      playList: [],
      pathList: ["myartist", "mymv", "myradio", "myplaylist", "myplaylist"],
      myList: ["我的歌手", "我的视频", "我的电台", "创建歌单", "收藏的歌单"],
      countList: [
        "artistCount",
        "mvCount",
        "djRadioCount",
        "createdPlaylistCount",
        "subPlaylistCount",
      ],
    };
  },
  computed: {
    userId() {
      return this.$store.state.userId;
    },
    createPLC() {
      return this.playList.slice(0, this.subCount.createdPlaylistCount);
    },
    subPLC() {
      return this.playList.slice(this.subCount.createdPlaylistCount);
    },
    createFlag() {
      return this.$store.state.createFlag;
    },
  },
  created() {
    this.getSubCount();
    this.getPlayList(this.userId);
  },
  methods: {
    toggleMyPage(path, index) {
      this.currentIndex = index;
      if (index > 2) {
        let flag = this.openIndex[index - 3];
        this.openIndex.splice(index - 3, 1, !flag);
      }
      if (index < 3) {
        this.$router.push({
          name: path,
        });
      }
    },
    getSubCount() {
       let timestamp = new Date().getTime();
      return request({
        url: `/user/subcount?timestamp=${timestamp}`,
      }).then((response) => {
        this.subCount = response;
      });
    },
    getPlayList(id) {
      let timestamp = new Date().getTime();
      return request({
        url: `/user/playlist?uid=${id}&timestamp=${timestamp}`,
      }).then((response) => {
        this.playList = response.playlist;
        this.$store.commit("changeValue", [
          {
            type: "userPlayList",
            value: this.createPLC,
          },
          {
            type: "createFlag",
            value: false,
          },
        ]);
        console.log(this.playList);
      });
    },
    createPlayList() {
      this.$store.commit("changeValue", {
        type: "createShow",
        value: true,
      });
    },
  },
};
</script>

<style>
.mpnew {
  position: absolute;
  top: 8px;
  left: 175px;
  background: linear-gradient(0deg, #f1f1f1, #fff);
  border: solid 1px #c4c4c4;
  padding: 0 7px;
  line-height: 20px;
  font-size: 12px;
  border-radius: 5px;
  cursor: pointer;
  font-weight: initial;
}
.mpnew:hover {
  background: linear-gradient(0deg, #f9f9f9, #fff);
  border: solid 1px #d5d5d5;
}
.mpnew > span:first-of-type {
  font-size: 16px;
  color: #be2914;
  margin-right: 3px;
  font-weight: bolder;
}
.mpreg {
  font-size: 12px;
  font-weight: 100;
  position: relative;
  top: -5px;
}
.mpimg {
  position: absolute;
  left: 25px;
  top: 3px;
}
.mpitem > div {
  width: 200px;
  padding-left: 40px;
  margin-left: -40px;
}
.mpactive > div,
.mpitem:hover > div:first-of-type {
  background-color: #eee;
  cursor: pointer;
}

.mpitem {
  padding-left: 40px;
  font-weight: bold;
  min-height: 38px;
  line-height: 38px;
  font-size: 14px;
  position: relative;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.mynav::-webkit-scrollbar-thumb {
  background-color: #ececec;
  border: solid 1px #d5d5d5;
  border-radius: 10px;
}
.mynav::-webkit-scrollbar-thumb:hover {
  background-color: #898989;
  border: solid 1px #6e6e6e;
}
.mynav::-webkit-scrollbar {
  width: 8px;
}
.mynav::-webkit-scrollbar:hover {
  background: linear-gradient(
    #ccc,
    #d7d7d7,
    #dfdfdf,
    #e4e4e4,
    #dfdfdf,
    #d7d7d7,
    #d1d1d1,
    #d5d5d5
  );
}
.mynav {
  width: 240px;
  display: flex;
  flex-direction: column;
  background-color: #f9f9f9;
  padding-top: 40px;
  height: 100vh;
  overflow-y: auto;
  overflow-x: hidden;
  border-right: solid 1px #d3d3d3;
}
</style>