<template>
  <div class="pagemain">
    <div class="pmain" v-if="showFlag">
      <div class="nav">
        <my-page-nav />
      </div>
      <div class="view">
        <router-view />
      </div>
    </div>
    <div v-else class="error">请使用较低版本的谷歌浏览器（78版本以前）或<a href="https://www.microsoft.com/zh-cn/edge?form=MY01BV&OCID=MY01BV">新版Edge浏览器</a></div>
  </div>
</template>

<script>
import { request } from "../../network/request";
import MyPageNav from "./MyPageNav";
export default {
  name: "MyPage",
  data() {
    return {
      showFlag: false,
    };
  },
  components: {
    MyPageNav,
  },
  created() {
    this.$router.push({
      name: "myartist",
    });

    request({
      url: "/user/subcount",
    }).then((response) => {
      if (response.code === 301) {
        this.showFlag = false;
      } else {
        this.showFlag = true;
      }
    });
  },
  computed: {
    createFlag() {
      return this.$store.state.createFlag;
    },
  },
};
</script>

<style>
.error {
  line-height: 100px;
  text-align: center;
  height: 72vh;
  width: 100vw;
  font-size: 20px;
}
.view {
  margin-left: 5px;
}
.pmain {
  width: 980px;
  background-color: #fff;
  margin-left: 25%;
  display: flex;
  border: solid 1px #d3d3d3;
}
</style>